.MPRankingPage {
    display: flex;
    gap: 1rem;
    padding: 1rem;
    width: 28vw;
    background-color: #282c34;
    border: 1px solid #EFE7E7;
    color: #EFE7E7;
    border-radius: 8px;
}

@media (max-width: 1100px) {
    .MPRankingPage {
        flex-direction: column;
        width: 28vw;
        margin: 0 auto;
    }

}

@media (max-width: 768px) {
    .MPRankingPage {
        flex-direction: column;
        width: 70vw;
        margin: 0 auto;
    }

}

.rankingSection {
    flex: 1;
    border-radius: 8px;
}

.Ranking_span{
    color: rgb(239, 231, 231);
    width: 100%;
    display: flex;
    margin-top: 2vh;
    justify-content: center;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 4vh;
}

.rankingList {
    list-style: none;
    padding: 0;
    margin: 0;
}
.rankingList li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.4rem 0;
    font-size: 14px;
    margin-bottom: 2vh;
}

.rankingList li:last-child {
    border-bottom: none;
}

.rank {
    font-weight: bold;
    text-align: center;
}
.name {
    flex: 1;
    padding: 0 0.5rem;
    text-align: left;
    width: 5vw;
}
.count {
    min-width: 2rem;
    display: flex;
    justify-content: center;
}

.rank-item.top1 { background: rgba(255,215,0,0.2); }   /* 골드 */
.rank-item.top2 { background: rgba(192,192,192,0.2); } /* 실버 */
.rank-item.top3 { background: rgba(205,127,50,0.2); }  /* 브론즈 */
.rank-item.top4 { background: rgba( 80, 80, 80,0.2); }  /* 4위 회색 계열 */
.rank-item.top5 { background: rgba( 60, 60, 60,0.2); }  /* 5위 더 연한 회색 */
