/* src/css/AdminMyDataPage.css */

/* 전체 페이지 레이아웃 */
.admin-pending-container {
    background-color: #212121;
    height: 100%;
}

.admin-pending-titlebox{
    text-align: center;
    align-content: center;
    width: 100%;
    height: 8vh;
    border-bottom: 1px solid #EFE7E7;
    margin-bottom: 2vh;
}

.admin-pending-title{
    color: #EFE7E7;
    font-size: 18px;
    font-weight: bold;
    margin-top: 2vh;
}

.admin-pending-container h2 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    border-bottom: 2px solid #ddd;
    padding-bottom: 0.5rem;
}

/* 리스트 스타일 제거 및 항목 간 간격 조정 */
.admin-pending-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.admin-pending-item {
    justify-items: center;
    align-items: center;
    border-bottom: 1px solid #ddd;
    margin-bottom: 20px;
}

.admin-pending-item:last-child {
    border-bottom: none;
}

/* 사진 썸네일 */
.admin-pending-item img {
    width: 700px;
    height: 500px;
    object-fit: cover;
    border-radius: 4px;
    margin-right: 1rem;
    background: #f0f0f0;
}

@media (max-width: 768px) {
    .admin-pending-item img{
        width: 200px;
        height: 180px;
    }

}

/* 일정·사용자 정보 */
.admin-pending-info {
    flex-grow: 1;
    font-size: 18px;
    color: #ddd;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 10px;
}

/* 버튼 그룹 */
.admin-pending-actions {
    display: flex;
    gap: 0.5rem;
}

/* 승인/거절 버튼 스타일 */
.admin-pending-actions button {
    padding: 0.4rem 0.8rem;
    font-size: 0.875rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s;
    margin-bottom: 20px;
}

.admin-pending-actions button:hover {
    opacity: 0.9;
}

.admin-pending-actions button:first-of-type {
    background-color: #4caf50;
    color: white;
}

.admin-pending-actions button:last-of-type {
    background-color: #f44336;
    color: white;
}

/* AdminMyDataPage.css */

/* 오버레이: 화면 전체, 반투명 검정 */
.Admin-image-preview-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.75);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/* 모달 컨텐츠 */
.Admin-image-preview-content {
    position: relative;
}

.Admin-image-preview-content img {
    max-width: 90vw;
    max-height: 90vh;
    border-radius: 4px;
}

/* 닫기 버튼 */
.Admin-modal-close {
    position: absolute;
    top: -10px;
    right: -10px;
    background: white;
    border: none;
    font-size: 1.5rem;
    line-height: 1;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    cursor: pointer;
}
