.MainPage{
    background-color: #212121;
}

.MainPage_homeimage {
    width: 50vw;
    aspect-ratio: 16/9;
    object-fit: cover; /* 선택 사항: 이미지의 내용을 영역에 맞게 크롭 */
}

/* 컨테이너(프레임) */
.slider-container {
    position: relative;
    width: 50vw;
    aspect-ratio: 16/9;
    margin: 2vh auto;
    overflow: visible;
    border: 3px solid #730f11;
    border-radius: 12px;
}

.slider-container::before {
    inset: -12px;      /* 사방 12px 씩 */
    transform: rotate(2deg);
}
.slider-container::after {
    inset: -6px;       /* 사방 6px 씩 */
    transform: rotate(-2deg);
}

/* 이미지 */
.slider-container img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 12px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.6);
    animation: float-slide 13s ease-in-out infinite;
}




.MainPageTop{
    justify-content: center;
    display: flex;
    margin-top: 2vh;
}

.MainPagebottom{
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 5vw;
}

@media (max-width: 768px) {
    .MainPagebottom {
        flex-direction: column;
        gap: 2vh; /* 수직 간격을 적절하게 조절 */
        height: auto; /* 필요한 경우 높이를 자동으로 지정 */
    }
}

@media (max-width: 768px) {
    .MainPage_homeimage {

        width: 80%;
        aspect-ratio: 16/9;
        object-fit: cover; /* 선택 사항: 이미지의 내용을 영역에 맞게 크롭 */
    }

    .slider-container{
        width: 80vw;
    }
}

.Main_footer{
    background-color: #212121;
    height: 20vh;
    color: #EFE7E7;
    margin-top: 50px;
    text-align: center;
}

.footer-link {
    color: #ff4d4f;      /* 원하는 색상 코드로 변경하세요 */
    text-decoration: none;
}

.footer-link:hover {
    color: #d9363e;      /* 호버 시 다른 색으로도 설정 가능 */
    text-decoration: underline;
}